<!--商品分类推荐没做 过几天再完成它-->
<template>
  <div>
    <cms-breadcrumb to="/productCategory/list" type='list'>
      <li>{{$route.name}}-{{$route.query.typeName}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout"  v-loading='loading'>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent>
        <el-form-item label="分类名称：" prop="nameAlias">
          {{$route.query.typeName}}
        </el-form-item>
        <el-form-item label="顶级分类导航推荐：" prop="topCateIds subCateIds">
          <category-cascader v-model="formData.subCateIds"
          :limit="6"
          :parentId="$route.query.id"
          ></category-cascader>
          <div class="gray">
            分类导航推荐将显示在商城导航，为保证商城布局正常显示，推荐最多选择6个（只能对子分类进行设置）
          </div>
        </el-form-item>
        <el-form-item label="分类推荐" prop="topCateIds">
          <category-cascader v-model="formData.topCateIds"
          :limit="8"
          :parentId="$route.query.id"
          ></category-cascader>
          <div class="gray">
            分类推荐将显示在商城导航侧栏顶部，为保证商城布局正常显示，推荐最多选择8个（只能对子分类进行设置）
          </div>
        </el-form-item>
        <el-form-item label="品牌推荐：" prop="brandIds">
          <cms-brand v-model="formData.brandIds" :multiple="true" :select="formData.brandSet" v-if="status">
            <el-button size='mini' type="primary">选择品牌</el-button>
          </cms-brand>
        </el-form-item>
        <el-form-item label="广告图1：" prop="resourcesSpaceId">
          <cms-picture-zone v-model="formData.resourceDataOneId" :data="formData.advertisingOne" v-if="status">
            <cms-button size='small' type='info' icon='correlation'>选择图片</cms-button>
          </cms-picture-zone>
          <div class="gray">
            广告图显示在品牌推荐下方，为保证显示效果，推荐使用220*220
          </div>
        </el-form-item>
        <el-form-item label="广告图2：" prop="resourcesSpaceId">
          <cms-picture-zone  v-model="formData.resourceDataTwoId" :data="formData.advertisingTwo" v-if="status">
            <cms-button size='small' type='info' icon='correlation'>选择图片</cms-button>
          </cms-picture-zone>
          <div class="gray">
            广告图显示在品牌推荐下方，为保证显示效果，推荐使用220*220
          </div>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button  v-prem="'/productCategory/save'" @click="save(true)" v-color>保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import categoryCascader from './../../../../components/cms/categoryCascader'
import {
  productCategoryNavGet,
  productCategoryNavUpdate
} from '@/api/product/category.js'
var defaultData = {
  'brandIds': [],
  'id': '',
  'resourceDataOneId': '',
  'resourceDataTwoId': '',
  'subCateIds': [],
  'topCateIds': []
}
export default {
  mixins: [form],
  components: {
    categoryCascader
  },
  data() {
    return {
      loading: false,
      formData: Object.assign({}, defaultData),
      rules: {
        cateName: [rules.required()],
        show: [rules.required()],
        virtual: [rules.required()],
        templatePath: [rules.required()],
        productTypeId: [rules.required('必须选择一个类型', 'change')],
        bondPrice: [rules.required(), rules.number()],
        commissionRate: [rules.required(), rules.number()]
      },
      productTypeItems: []
    }
  },
  methods: {
    getFormData(id) {
      this.loading = true
      productCategoryNavGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = Object.assign(defaultData, res.data)
          this.formData.subCateIds = res.data.subCateSet.map(v => v.id)
          this.formData.topCateIds = res.data.topCateSet.map(v => v.id)
          this.loading = false
        } else {
          this.loading = false
        }
      })
    },
    save(state) {
      const params = {
        id: this.formData.id,
        resourceDataOneId: this.formData.resourceDataOneId,
        resourceDataTwoId: this.formData.resourceDataTwoId,
        brandIds: this.formData.brandIds.map(v => v).toString(),
        subCateIds: this.formData.subCateIds.map(v => v).toString(),
        topCateIds: this.formData.topCateIds.map(v => v).toString()
      }
      this.saveForm(
        productCategoryNavUpdate,
        params,
        state,
        '/productCategory/list',
        'list'
      ).then(res => {})
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.destroyed()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>




